$well-color: #aaa;
$well-padding: 8px;
$well-heading-height: 36px;

.well {
	width: 300px;
	position: relative;
	display: inline-block;
	vertical-align: top;
	padding: $well-padding;
	border: 1px dashed $well-color;
	margin: $well-heading-height ($well-padding/2) ($well-padding*2);

	&.is-active {
		border-style: solid;
	}

	&-heading {
		position: absolute;
		top: -$well-heading-height;
		height: $well-heading-height;
		line-height: $well-heading-height;
		padding: 0 $well-padding;
		color: darken($well-color, 25);
		border: 1px dashed $well-color;
		border-bottom: 1px solid #fff;

		.is-active & {
			border-style: solid;
		}
	}

	&-card {
		background: #ccc;
		border-radius: 3px;
		padding: $well-padding;
		list-style-type: none;
		cursor: pointer;

		& + & {
			margin-top: $well-padding;
		}
	}
}